import React, { useState } from "react";
import { Hidden } from "@material-ui/core";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import { Grid } from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Button from "@material-ui/core/Button";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import moment from "moment";

const SearchSectionDesktop = () => {
  const [value, setValue] = useState(0);
  const [calander, setCalander] = useState(null);
  const [showCalander, setShowCalander] = useState(false);
  let handelDayClick = (e) => {
    let dateValue = moment(e).format("yyyy-MM-DD");
    setCalander(dateValue);
    setShowCalander(false);
  };

  let handelCalanderShow = () => {
    setShowCalander(!showCalander);
  };

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const [showMenuBar, setshowMenuBar] = useState(false);
  const [menuText, setMenuText] = useState("Select");

  let handelSelector = () => {
    setshowMenuBar(!showMenuBar);
  };

  let handelMenuClick = (e) => {
    setMenuText(e.target.textContent);
    setshowMenuBar(false);
  };
  let handelCalanderChange = () => {};
  return (
    <Hidden only={["sm", "xs"]}>
      <Grid container className="searchSectionMainContainerDesktop">
        <Grid item xs={12}>
          <Grid
            container
            style={{ padding: "8px" }}
            className="customCard"
            spacing={3}
          >
            <Grid item xs={2}>
              <div id="searchinputmainDesktop">
                <input
                  placeholder="2021-04-22"
                  className="searchInput"
                  value={calander !== null ? calander : ""}
                  onChange={handelCalanderChange}
                />
                <ExpandMoreIcon
                  id="searchinputicon"
                  onClick={handelCalanderShow}
                />
                <div
                  style={{
                    position: "absolute",
                    top: "45px",
                    display: showCalander ? "flex" : "none",
                    zIndex: "50",
                    overflow: "auto",
                  }}
                >
                  <Calendar onClickDay={handelDayClick} />
                </div>
              </div>
            </Grid>
            <Grid item xs={2}>
              <div id="searchinputmain">
                <button className="searchSelector">{menuText}</button>
                {showMenuBar && (
                  <div className="selectorMenu">
                    <p onClick={handelMenuClick}>Volvo</p>
                    <p onClick={handelMenuClick}>Saab</p>
                    <p onClick={handelMenuClick}>Mercedes</p>
                    <p onClick={handelMenuClick}>Audi</p>
                  </div>
                )}

                <ExpandMoreIcon id="searchinputicon" onClick={handelSelector} />
              </div>
            </Grid>
            <Grid item xs={6}>
              <div id="searchinputmain">
                <input
                  placeholder="YouTube Top 20 Search"
                  className="searchInput"
                />
                <SearchIcon id="searchinputicon" />
              </div>
            </Grid>
            <Grid
              item
              xs={2}
              style={{
                display: "flex",
                justifyContent: "center",
                paddingRight: "5px",
              }}
            >
              <Button
                variant="contained"
                color="primary"
                style={{ height: "37px" }}
              >
                Search
              </Button>
            </Grid>
            <Grid item xs={12}>
              <Tabs
                onChange={handleChange}
                textColor="primary"
                variant="scrollable"
                aria-label="scrollable auto tabs example"
                id="desktopTab"
                value={value}
              >
                <Tab label="All" id="tabStyleActive" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
                <Tab label="Filter" id="tabStyle" />
              </Tabs>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Hidden>
  );
};

export default SearchSectionDesktop;
